    private connected$ = new Subject<any>();
    private connectedFE$ = new Subject<any>();

    constructor() {
    }

    public connect(url: string): any {
        if (!this.subject) {
            this.subject = this.create(url);
            console.log('Successfully connected: ' + url);
            this.connected$.next(true);
        }
        return this.subject;
    }

    public connectFE(url: string): any {
        if (!this.subjectFE) {
            this.subjectFE = this.create(url);
            console.log('connectFE Successfully connected: ' + url);
            this.connectedFE$.next(true);
        }
        return this.subjectFE;
    }

    public connected(): Observable<any> {
        return this.connected$.asObservable();
    }

    public connectedFE(): Observable<any> {
        return this.connectedFE$.asObservable();
    }

