import {AfterViewInit, Component, ElementRef, Input, OnChanges, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {
    CdkDrag,
    CdkDragStart,
    CdkDropList, CdkDropListGroup, CdkDragMove, CdkDragEnter,
    moveItemInArray
} from '@angular/cdk/drag-drop';
import {ViewportRuler} from '@angular/cdk/overlay';
import {Camera} from '../../../shared/model/main';
import {PagesService} from '../../../shared/service/pages.service';
import {checkMobileMode} from '../../../shared/utils/convert.util';

@Component({
    selector: 'app-drop-drap',
    templateUrl: './drop-drap.component.html',
    styleUrls: ['./drop-drap.component.scss']
})
export class DropDrapComponent implements OnInit, AfterViewInit {
    @ViewChild(CdkDropListGroup, {static: true}) listGroup: CdkDropListGroup<CdkDropList>;
    @ViewChild(CdkDropList, {static: true}) placeholder: CdkDropList;

    public items: Array<number> = [1, 2, 3, 4, 5, 6, 7, 8, 9];

    public target: CdkDropList | any;
    public targetIndex: number;
    public source: CdkDropList | any;
    public sourceIndex: number;
    public dragIndex: number;
    public activeContainer: any;

    listCamera: Array<Camera> = new Array<Camera>();
    loading: any = [];
    isShakeHands: any = [];
    arrConnected: any = [];
    editListCam: any = [];
    mutedList: any = [];
    turnServer = 'aivisvn.ddns.net:3478';

    isMobileMode: boolean;
    public base64tokenRes = 'Wait connect';

    constructor(
        private viewportRuler: ViewportRuler,
        private pageService: PagesService
    ) {
        this.target = null;
        this.source = null;
        this.isMobileMode = checkMobileMode();
    }

    ngAfterViewInit() {
        const phElement: any = this.placeholder.element.nativeElement;

        phElement.style.display = 'none';
        phElement.parentElement.removeChild(phElement);
    }

    add() {
        this.items.push(this.items.length + 1);
    }

    shuffle() {
        this.items.sort(() => {
            return .5 - Math.random();
        });
    }

    dragMoved(e: CdkDragMove) {
        const point = this.getPointerPositionOnPage(e.event);

        this.listGroup._items.forEach(dropList => {
            if (__isInsideDropListClientRect(dropList, point.x, point.y)) {
                this.activeContainer = dropList;
                return;
            }
        });
    }

    dropListDropped(e: any) {
        if (!this.target) {
            return;
        }

        const phElement = this.placeholder.element.nativeElement;
        const parent: any = phElement.parentElement;

        phElement.style.display = 'none';

        parent.removeChild(phElement);
        parent.appendChild(phElement);
        parent.insertBefore(this.source.element.nativeElement, parent.children[this.sourceIndex]);

        this.target = null;
        this.source = null;

        if (this.sourceIndex != this.targetIndex) {
            moveItemInArray(this.items, this.sourceIndex, this.targetIndex);
        }
    }

    dropListEnterPredicate = (drag: CdkDrag, drop: CdkDropList) => {
        if (drop == this.placeholder) {
            return true;
        }

        if (drop != this.activeContainer) {
            return false;
        }

        const phElement = this.placeholder.element.nativeElement;
        const sourceElement: any = drag.dropContainer.element.nativeElement;
        const dropElement: any = drop.element.nativeElement;

        const dragIndex = __indexOf(dropElement.parentElement.children, (this.source ? phElement : sourceElement));
        const dropIndex = __indexOf(dropElement.parentElement.children, dropElement);

        if (!this.source) {
            this.sourceIndex = dragIndex;
            this.source = drag.dropContainer;

            phElement.style.width = sourceElement.clientWidth + 'px';
            phElement.style.height = sourceElement.clientHeight + 'px';

            sourceElement.parentElement.removeChild(sourceElement);
        }

        this.targetIndex = dropIndex;
        this.target = drop;

        phElement.style.display = '';
        dropElement.parentElement.insertBefore(phElement, (dropIndex > dragIndex
            ? dropElement.nextSibling : dropElement));

        this.placeholder.enter(drag, drag.element.nativeElement.offsetLeft, drag.element.nativeElement.offsetTop);
        return false;
    };

    /** Determines the point of the page that was touched by the user. */
    getPointerPositionOnPage(event: MouseEvent | TouchEvent) {
        // `touches` will be empty for start/end events so we have to fall back to `changedTouches`.
        const point = __isTouchEvent(event) ? (event.touches[0] || event.changedTouches[0]) : event;
        const scrollPosition = this.viewportRuler.getViewportScrollPosition();

        return {
            x: point.pageX - scrollPosition.left,
            y: point.pageY - scrollPosition.top
        };
    }

    ngOnInit(): void {
        this.getListCamera();
    }

    getListCamera() {
        this.pageService.fetchCameraListWithSetting().subscribe((data) => {
            const res = data.cams || [];
            if (res && res.length > 0) {
                this.listCamera = res;
                this.pageService.setData(this.listCamera);

                this.listCamera.forEach(x => {
                    this.isShakeHands.push(false);
                    this.mutedList.push(false);
                    this.loading.push(false);
                    this.editListCam.push({
                        name: x.setting ? x.setting.name : '',
                        isEdit: false
                    });
                });
            }
        });
    }

    shakeHands(cameraObj: Camera, index: number) {
        this.loading[index] = true;
        this.pageService.getTurnCredential().subscribe(data => {
            if (data) {
                this.turnServer = data.turnServer;
                const pcConfig = {
                    iceServers: [
                        {
                            urls: 'stun:' + data.turnServer
                        },
                        {
                            urls: 'turn:' + data.turnServer,
                            username: data.username,
                            credential: data.password,
                        }
                    ]
                };

                const pc = new RTCPeerConnection(pcConfig);
                const log = (msg: any) => {
                    // document.getElementById('log' + cameraObj.cameraId).innerHTML += msg + '<br>';
                };

                pc.ontrack = (event) => {
                    const el = document.createElement(event.track.kind);
                    // @ts-ignore
                    el.srcObject = event.streams[0];
                    // @ts-ignore
                    el.autoplay = true;
                    // @ts-ignore
                    el.controls = true;
                    // @ts-ignore
                    el.muted = true;

                    if (event.track.kind === 'audio') {
                        // @ts-ignore
                        el.controls = false;
                        // @ts-ignore
                        el.muted = true;
                        // el.play();
                        this.mutedList[index] = false;
                    }

                    // @ts-ignore
                    document.getElementById('cam' + cameraObj.cameraId).appendChild(el);

                    setTimeout(() => {
                        this.setAttributeVideo('cam' + cameraObj.cameraId, 'width', '100%');

                        if (!this.isMobileMode) {
                            // @ts-ignore
                            document.getElementById('cam' + cameraObj.cameraId).children[0].controls = false;
                            // @ts-ignore
                            document.getElementById('cam' + cameraObj.cameraId).children[0].style.pointerEvents = 'none';
                        }

                    }, 300);
                };

                pc.oniceconnectionstatechange = e => log(pc.iceConnectionState);
                pc.onicecandidate = event => {
                    if (event.candidate === null) {
                        // console.log('base64 json', JSON.stringify(pc.localDescription));
                        const base64token = btoa(JSON.stringify(pc.localDescription));

                        this.pageService.requestStreamSignalWithTurn(cameraObj.cameraId, base64token, this.turnServer).subscribe(res => {
                            if (res.token) {
                                this.base64tokenRes = res.token;
                                // console.log('base64tokenRes', this.base64tokenRes);

                                // start ontrack connect
                                pc.setRemoteDescription(new RTCSessionDescription(JSON.parse(atob(res.token))));

                                if (this.arrConnected.find((x: any) => x.id === index)) {
                                    this.arrConnected.splice(this.arrConnected.indexOf(this.arrConnected.find((x: any) => x.id === index)), 1);
                                }
                                this.arrConnected.push({obj: pc, id: index});
                                this.isShakeHands[index] = true;
                                this.loading[index] = false;

                            }
                        }, error => {
                            this.loading[index] = false;
                        });
                    }

                };

                // Offer to receive 1 audio, and 1 video track
                pc.addTransceiver('video', {direction: 'sendrecv'});
                pc.addTransceiver('audio', {direction: 'sendrecv'});
                pc.createOffer().then(d => pc.setLocalDescription(d)).catch(log);
            }
        }, error => {
            this.loading[index] = false;
        });
    }

    setAttributeVideo(id: string, attributeName: string, value: string) {
        // @ts-ignore
        document.getElementById(id).children[0].setAttribute(attributeName, value);
    }
}

function __indexOf(collection: any, node: any) {
    return Array.prototype.indexOf.call(collection, node);
}

/** Determines whether an event is a touch event. */
function __isTouchEvent(event: MouseEvent | TouchEvent): event is TouchEvent {
    return event.type.startsWith('touch');
}

function __isInsideDropListClientRect(dropList: CdkDropList, x: number, y: number) {
    const {top, bottom, left, right} = dropList.element.nativeElement.getBoundingClientRect();
    return y >= top && y <= bottom && x >= left && x <= right;
}
